/*------------------------------------*\
     NAV TABS
\*------------------------------------*/

.nav-tabs{ 
    @include justify-content(center);

    .nav-item{
        margin: 0 5px -1px 0;
        cursor: pointer;

        .nav-link{
            min-height: 56px;
            height: 100%;
            @include flexbox();
            @include align-items(center);
            @include flex-direction(row);
            padding: 10px 3rem;
            color: $dark-grey;
            background: $light-grey;
            border: 1px solid $border-grey;
            border-right: 1px solid $border-grey;
            border-bottom-color: $white;
            border-radius: 0;
            &.active{
                background: transparent;
                border-bottom-color: $white;
            }
            &:hover, &:active, &:focus{
                background: $bg-white;
            }
        }
        &:last-child .nav-link{
            border-right: 1px solid $border-grey;
        }
        &:first-child .nav-link.active{
            border-right-color: $border-grey;
            background: $bg-white;
        }

        &:last-child .nav-link.active{
            border-left-color: $border-grey;
        }
        &:not(:first-child):not(:last-child) .nav-link.active{
            border-left-color: $border-grey;
            border-right-color: $border-grey;
        }
    }
}
@media screen and (max-width:600px){
    .nav-tabs{ 
        .nav-item{

            .nav-link{
                padding: 10px 1rem;
            }
        }
    }
}
@media screen and (max-width:400px){
    .nav-tabs{ 
        .nav-item{
            width: 50%;
            margin: 0;
            .nav-link{
                padding: 5px 1rem;
                min-height: 40px;
                border-bottom: 0;
            }
        }
    }
}